/*!--------------------------------------------------------------
# color
--------------------------------------------------------------*/
/* color list */


$cl-primary: #22d;
$cl-secondary: adjust-hue($cl-primary,90deg);
$cl-gray: desaturate($cl-primary,60%);
$cl-black: darken($cl-gray, 45%);
$clwhite: lighten($cl-gray, 45%);

$vars: 5, 10, 15, 20, 25, 30;

@each $var in $vars {
  .bg-primary-#{$var} {
    background: saturate($cl-primary, $var);
  }
}